#movie {
    /* width: 45%; */
    /* height: 200px; */
    /* margin-bottom: 10px; */
    /* box-sizing: border-box; */
    padding: 8px;
    margin: 0 5px 15px 0;
    background-color: rgba(0, 0, 0, 0.1);
    box-shadow: 0 0 20px black;
}

#movie .contain {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

#movie .contain img {
    width: 50%;
    height: 100%;
}

#movie .contain .info {
    color: #fff;
    width: 50%;
    padding: 0 10px 0 10px;
}

#movie .contain .info .title {
    font-size: 30px;
    margin-bottom: 10px;
}

@media screen and (max-width: 700px) {
    #movie {
        /* text-align: center; */
        margin: 0 0 30px 0;
    }

    #movie .contain {
        display: block;
    }

    #movie .contain img {
        width: 100%;
    }

    #movie .contain .info {
        width: 100%;
        text-align: center;
        margin: 10px 0 10px 0;
        padding: 0;
        font-size: 15px;
    }

    #movie .contain .info .title {
        font-size: 20px;
        padding-bottom: 8px;
        box-sizing: border-box;
        border-bottom: 1px solid #fff;
    }
}
